.typing-game {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
}

.randomText {
    font-size: 2rem;
    margin: 3rem 2rem 3rem 2rem;
    height: 15rem;
    overflow: auto;
    text-align: center;
}

.input {
    font-size: 1.5rem;
    padding: 1rem;
    width: 70%;
    line-height: 100%;
    overflow: hidden;
    white-space: nowrap;
}

.timer {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.buttons {
    display: flex;
    justify-content: center;
}

.start {
    padding: 1rem 2rem;
    background-color: lightgreen;
    border: none;
    border-radius: 5px;
    font-size: 1.5rem;
    margin-right: 1rem;
}

.reset {
    padding: 1rem 2rem;
    background-color: lightcoral;
    border: none;
    border-radius: 5px;
    font-size: 1.5rem;
}

.correct {
    color: rgb(97, 255, 97);
}

.incorrect {
    color: rgb(255, 51, 51);
}